.skills {

  .container {
    flex-direction: column;

    @include media-q(min, 768) {
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-between;
    }
  }

  &__item {
    background: linear-gradient(90deg, #AAF1F8 -2.49%, #F3B3CB 51.42%, #F8CEA4 100%);
    border-radius: 20px;
    margin-bottom: 35px;
    padding: 40px 30px;
    flex-direction: column;

    @include media-q(min, 768) {
      width: 49%;
      padding: 50px 60px;
      align-items: center;
      flex-direction: row;
    }

    &:last-child {
      justify-content: space-between;
    }

    &:last-child {
      width: 100%;
    }

    &-title {
      font-size: 24px;
      font-weight: 600;
      font-style: italic;

      .skills__item:not(:last-child) & {
        margin-bottom: 30px;

        @include media-q(min, 768) {
          margin-bottom: 40px;
        }
      }

      .skills__item:last-child & {
        margin-right: 50px;
        margin-bottom: 30px;

        @include media-q(min, 768) {
          margin-bottom: 0;
        }

        @include media-q(min, 1024) {
          margin-right: 150px;
        }
      }
    }

    ul {
      flex-direction: column;
      width: 100%;

      @include media-q(min, 768) {
        flex-direction: row;
        justify-content: space-between;
        flex-wrap: wrap;
      }

      li {
        font-size: 18px;
        position: relative;

        &::before {
          content: 'âœ“';
          margin-right: 10px;
        }
      }
    }
  }
}

.skills__item:not(:last-child) ul li {
  margin-bottom: 15px;
}

.skills__item:last-child ul li {
  margin-bottom: 15px;

  @include media-q(min, 768) {
    margin-bottom: 0;
    width: 50%;
  }

  &:nth-child(1) {

    @include media-q(min, 768) {
      margin-bottom: 15px;
    }

    @include media-q(min, 1024) {
      margin-bottom: 0;
    }
  }

  @include media-q(min, 1024) {
    width: auto;
    margin-bottom: 0;
  }
}