.hero-section {

  .container {
    flex-direction: column;

    @include media-q(min, 768) {
      flex-direction: row;
    }
  }

  &__img {
    margin-bottom: 20px;

    @include media-q(min, 768) {
      margin-right: 60px;
      width: 30%;
      margin-bottom: 0;
    }
  }

  &__content {

    @include media-q(min, 768) {
      width: 80%;
      max-width: 550px;
    }
  }

  .btn--link {
    margin-top: 30px;

    @include media-q(min, 1024) {
      margin-top: 70px;
    }
  }
}