.slick-dots {
  display: flex;
  justify-content: center;
  margin-top: 20px;

  @include media-q(min, 768) {
    margin-top: 50px;
  }

  li {
    button {
      font-size: 0;
      width: 15px;
      height: 15px;
      border-radius: 50%;
      background-color: #ECECEC;
      margin: 0 10px;

      @extend %reset-button;
    }

    &.slick-active {
      button {
        background-color: $c-yellow;
      }
    }
  }
}