//-----------------------------------------------------------------------------
// Fonts variable(s).
$sans: Gilroy, Arial, sans-serif;

// Font weight variants
$f_light: 300;
$f_normal: 400;
$f_medium: 600;
$f_bold: 700;

//-----------------------------------------------------------------------------
// Image paths
$images: '../img/';

//-----------------------------------------------------------------------------
// Main theme colors

// Default colors
$white: #fff;
$black: #1C1C1C;

// Main site color palette
$c-blue: #4D90F4;
$c-yellow: #ffcf59;

// These colors for the Drupal messages style
// but also could be used for the other purposes
$red: #f4371e;
$green: #56c02b;
$orange: #f87c00;

//-----------------------------------------------------------------------------
// Other settings

// Font sizes
// Site base font size and line height
$fs-base: 18px;

$line-height-base: 1.14;

// Headings line-height
$headings-line-height: 1.1;
