.portfolio {

  &__tabs-nav {
    margin-bottom: 30px;

    @include media-q(min, 768) {
      margin-bottom: 60px;
    }

    .btn {
      border: 1px solid $c-blue;
      background: transparent;
      color: $black;
      width: 100%;
      margin-bottom: 20px;

      @include media-q(min, 768) {
        margin-right: 15px;
        margin-bottom: 0;
        width: inherit;
      }

      &.active {
        border: 1px solid transparent;
        background-color: $c-blue;
        color: $white;

        &:hover {
          border-color: transparent;
        }
      }

      &:hover {
        border-color: $black;
      }
    }
  }

  &__tabs-content {
    display: none;

    @include media-q(min, 768) {
      margin-left: -15px;
    }

    &.active {
      display: block;
    }
  }

  &__item {
    padding: 0 15px;

    &-img {
      background: linear-gradient(90deg, #AAF1F8 -2.49%, #F3B3CB 51.42%, #F8CEA4 100%);
      display: flex;
      justify-content: center;
      align-items: center;
      height: 300px;
      border-radius: 20px;
      margin-bottom: 20px;
      padding: 20px;
    }

    &-title {
      font-size: 18px;
      font-weight: 600;
      margin-bottom: 10px;
      max-width: 220px;

      @include media-q(min, 768) {
        font-size: 20px;
        max-width: 260px;
      }
    }

    &-category {
      margin-bottom: 10px;
    }

    .btn--link-secondary {
      margin-bottom: 10px;
      display: inline-block;
    }

    &-info {
      position: relative;

      .font-icon {
        position: absolute;
        right: 25px;
        top: 0;
        color: $c-yellow;

        &::before {
          font-size: 38px;
        }
      }
    }
  }
}