.header {
  padding: 15px 0;
  background-color: $white;
  box-shadow: 0 2px 14px rgba(0, 0, 0, .05);

  .container {
    flex-direction: column;

    @include media-q(min, 768) {
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
    }
  }

  &__logo {
    font-weight: $f-bold;
    align-items: center;
    margin-bottom: 20px;
    font-size: 13px;

    @include media-q(min, 768) {
      margin-bottom: 0;
      font-size: 18px;
    }

    .font-icon-logo {
      margin-right: 20px;
      color: $c-blue;
    }

    span {
      display: block;
      font-weight: $f-normal;
      font-style: italic;
    }
  }

  &__nav {
    display: none;

    @include media-q(min, 1024) {
      display: flex;
    }

    ul {
      display: flex;

      li {
        a {
          padding: 15px 30px;
          transition: color .2s ease;
        }

        &:hover {
          a {
            color: $c-blue;
          }
        }
      }
    }
  }
}
