%reset-button {
  border: none;
  outline: none;

}

%btn,
.btn {
  background-color: $c-blue;
  color: $white;
  border-radius: 5px;
  padding: 10px 40px;
  font-weight: $f-medium;
  transition: opacity .2s ease;
  display: inline-block;
  text-align: center;

  @extend %reset-button;

  &:hover {
    color: $white;
    opacity: .9;
  }
}

.btn--link {
  display: flex;
  align-items: center;
  color: $c-blue;
  font-size: 18px;
  font-weight: $f-medium;
  transition: opacity .2s ease;

  @extend %reset-button;

  &:hover {
    opacity: .8;
    color: $c-blue;
  }

  .font-icon-arrow {
    margin-left: 15px;
    &::before {
      transform: translateY(2px);
    }
  }
}

.btn--link-secondary {
  font-size: 16px;
  opacity: .6;
  color: $c-blue;
  text-decoration: underline;
}

.form-submit {
  @extend %btn;
  @include font-size(16);
  padding: 11px 24px 12px;
  min-width: 150px;
}

