.contacts {
  text-align: center;

  p {
    font-size: 24px;
    margin-bottom: 40px;
  }

  &__title {
    font-size: 28px;
    font-weight: $f-bold;
    margin-bottom: 55px;
    display: block;
  }

  &__item {
    padding: 0 7px;
    transition: opacity .2s ease;

    @include media-q(min, 768) {
      padding: 0 40px;
    }

    &:hover {
      opacity: .9;
    }
  }
}